<template>
  <section class="login-page">
    <!--TODO remplazar con el el $root-->
    <v-snackbar
      :timeout="8000"
      top
      multi-line="multi-line"
      vertical="vertical"
      v-model="snackbar.show"
      color="secundary"
    >
      {{snackbar.text}}
      <v-btn flat color="primary" @click.native="snackbar.show = false">Cerrar</v-btn>
    </v-snackbar>

    <v-container>
      <v-layout wrap class="pa-4">
        <v-form v-model="valid" @submit.prevent="login">
          <v-flex xs12 md6 offset-md3 class="text-xs-center">
            <img src="~@/assets/logo.png" alt="logo" height="200px">
          </v-flex>

          <v-flex xs12 md8 offset-md2>
            <v-text-field
              light
              label="Correo Electrnico"
              min="10"
              max="100"
              required
              :rules="EmailRules"
              type="email"
              v-model="email"
              counter
            ></v-text-field>
            <v-text-field
              light
              label="Introduce tu contrasea"
              hint="Al menos 8 caracteres"
              v-model="password"
              min="8"
              required
              :rules="RequiredRules('La contrasea es obligatoria')"
              :append-icon="e1 ? 'fa-eye' : 'fa-eye-slash'"
              @click:append="e1 = !e1"
              :type="e1 ? 'password' : 'text'"
              counter
            ></v-text-field>
          </v-flex>
          <v-flex class="text-xs-right">
            <!--<v-btn small :to="{name: 'ResetPass'}" :disabled="loading">Olvido la Contrasea?</v-btn>-->
            <!--<v-btn type="submit" :disabled="!valid || loading" :loading="loading">Entrar</v-btn>-->
            <v-btn :to="{name: 'home'}" :disabled="!valid || loading" :loading="loading">Entrar</v-btn>
          </v-flex>
        </v-form>
      </v-layout>
    </v-container>

    <v-footer absolute class="text-xs-center">Todos los derechos reservados &copy; 2019</v-footer>
  </section>
</template>

<script>
import { RequiredRules, EmailRules } from '../validator_rules'
import { VForm, VTextField, VSnackbar, VProgressCircular } from 'vuetify'

export default {
  data () {
    return {
      RequiredRules,
      EmailRules,
      valid: false,
      snackbar: {
        show: false,
        text: null
      },
      email: 'elijose.c@gmail.com',
      password: '123456',
      e1: true,
      loading: false
    }
  },
  methods: {
    login () {
      this.loading = true
      this.$store.commit('logout')
      this.$store.dispatch('login', {email: this.email, password: this.password})
        .then(
          response => {
            this.loading = false
            if (response.ok && response.status === 200 && response.data.api_token) {
              this.$store.commit('login', response.data)
              this.$store.dispatch('getMasters')
              this.$router.push({name: 'Main'})
            } else {
              this.snackbar.show = true
              this.snackbar.text = response.data
              this.loading = false
            }
          },
          (error) => {
            console.log(error)
            if (error.data.errors) {
              this.snackbar.text = error.data.errors
            } else {
              this.snackbar.text = 'Error de conexion, por favor verifica tu conexion'
            }
            this.snackbar.show = true
            this.loading = false
          }
        )
    }
  },
  components: {
    VForm,
    VTextField,
    VSnackbar,
    VProgressCircular
  }
}
</script>
