<template>
  <section class="add-page">
    <s-header>
      <span>Agregar Transaccin</span>
    </s-header>

    <v-container>
      <v-layout>
        <v-form v-model="valid" @submit.prevent="">
          <v-flex>
            <h3 class="text-xs-center">Datos de la Transaccin</h3>
          </v-flex>
          <v-flex>
            <v-text-field
              box
              light
              min="2000"
              class="amount"
              type="number"
              v-model="request.amount1"
              label="Monto"
              suffix="$"
              required
              :rules="[
                (v) => !!v || 'El monto es obligatorio',
                (v) =>  (v && v >= 2000) || 'El monto debe ser mayor a 2000 pesos'
              ]"
            ></v-text-field>
          </v-flex>
          <v-flex text-xs-center>
            <v-icon color="black" large>fas fa-exchange-alt</v-icon>
          </v-flex>
          <v-flex>
            <v-text-field
              box
              light
              min="3"
              class="amount"
              type="number"
              v-model="request.amount2"
              label="Monto"
              suffix="Bs.S"
              required
              :rules="[
                (v) => !!v || 'El monto es obligatorio',
                (v) =>  (v && v > 3) || 'El monto debe ser mayor a 3'
              ]"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <h3 class="text-xs-center">Datos Beneficiario</h3>
          </v-flex>
          <v-flex xs12 >
            <v-select
              light
              :items="[
                {value: 'V', text: 'Venezolano'},
                {value: 'E', text: 'Extranjero'},
                {value: 'J', text: 'Juridico'}
              ]"
              v-model="request.identification.type"
              class="text-xs-right"
              required
              :rules="RequiredRules('El tipo de identificacin es obligatorio')"
              single-line
              menu-props="bottom"
            ></v-select>
          </v-flex>
          <v-flex xs12>
            <v-text-field
              light
              label="Cdula de identidad o Rif"
              min="10"
              max="30"
              v-model="request.identification.number"
              type="number"
              required
              :rules="IdentificationRules"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <v-text-field
              light
              required
              label="Nombre del Beneficiario"
              v-model="request.full_name"
              :rules="FullNameRules"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <v-text-field
              light
              required
              label="Correo Electrnico"
              v-model="request.email"
              :rules="EmailRules"
            ></v-text-field>
          </v-flex>
          <v-flex>
            <h3 class="text-xs-center">Datos Bancarios</h3>
          </v-flex>
          <v-flex>
            <v-autocomplete
              light
              clearable
              return-object
              :items="banks"
              v-model="request.bank"
              label="Banco"
              item-text="name"
              :prepend-icon="bankIcon"
              required
              :rules="[(v) => !!v || 'El banco es obligatorio']"
            >
              <template slot="item" slot-scope="data">
                <template>
                  <v-list-tile-avatar>
                    <v-icon>{{data.item.icon}}</v-icon>
                  </v-list-tile-avatar>
                  <v-list-tile-content>
                    <v-list-tile-title v-html="data.item.name"></v-list-tile-title>
                  </v-list-tile-content>
                </template>
              </template>
            </v-autocomplete>
          </v-flex>
          <v-flex>
            <v-select
              light
              :items="account_types"
              v-model="request.type"
              label="Tipo de cuenta"
              item-text="text"
              item-value="value"
              required
              :rules="RequiredRules('El tipo de cuenta es obligatorio')"
              single-line
            ></v-select>
          </v-flex>
          <v-flex>
            <v-text-field
              light
              required
              type="number"
              :prefix="bankCode"
              counter="16"
              label="Numero de cuenta"
              v-model="request.account_number"
              :rules="BankAccountNumRules"
            ></v-text-field>
          </v-flex>

          <v-btn
            fixed
            dark
            fab
            bottom
            right
            type="submit"
            :loading="loading"
            :disabled="!valid"
            @click="addTransaction"
          >
            <v-icon>fas fa-save</v-icon>
          </v-btn>
        </v-form>
      </v-layout>
    </v-container>
  </section>
</template>

<script>
import SHeader from '../components/partial/SHeader'
import { RequiredRules, BankAccountNumRules, EmailRules, FullNameRules, IdentificationRules } from '../validator_rules'

export default {

  name: 'Add',

  data () {
    return {
      IdentificationRules,
      FullNameRules,
      EmailRules,
      RequiredRules,
      BankAccountNumRules,
      valid: false,
      loading: false,
      account_types: [
        {text: 'Ahorro', value: 'a'},
        {text: 'Corriente', value: 'c'}
      ],
      request: {
        amount1: 2000,
        amount2: 2000 * this.$store.state.parameters.rate.value,
        identification: {
          number: null,
          type: 'V'
        },
        full_name: null,
        email: null,
        bank: null,
        type: 'c',
        account_number: null,
        rate: null,
        created_at: null
      }
    }
  },
  watch: {
    amount1 (value) {
      this.request.amount2 = value * this.$store.state.parameters.rate.value
    },
    amount2 (value) {
      this.request.amount1 = value / this.$store.state.parameters.rate.value
    }
  },
  computed: {
    amount1 () {
      return this.request.amount1
    },
    amount2 () {
      return this.request.amount2
    },
    banks () {
      return this.$store.state.banks
    },
    bankCode () {
      return (this.request.bank && this.request.bank.code) ? this.request.bank.code : ''
    },
    bankIcon () {
      return (this.request.bank) ? this.request.bank.icon : 'fas fa-ban'
    }
  },
  methods: {
    addTransaction () {
      this.loading = true
      this.request.created_at = new Date()
      this.request.status = false
      this.request.rate = this.$store.state.parameters.rate
      this.$store.commit('addTransaction', this.request)
      this.$router.push({name: 'home'})
      // this.$store.dispatch('setTransaction', this.request).then(
      //   response => {
      //     if (response.ok && response.status === 200) {
      //       this.request.created_at = response.data.created_at
      //       this.$store.commit('setUserCredits', this.credits - this.request.amount)
      //       this.request.status = 'p'
      //       this.$store.commit('addTransaction', this.request)
      //       this.info.show = true
      //     }
      //   }
      // )
    }
  },
  components: {
    SHeader
  }
}
</script>

<style lang="stylus" scoped>
</style>
